.c-achievement {
    margin: 0 -32px 32px;
    padding-bottom: 32px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

    svg {
        width: 150px;
        display: block;
        margin: 32px auto 32px;
    }
    h1 {
        font-size: 22px;
        font-weight: 700;
        text-align: center;
    }

    &__heading {
        padding-top: $unit*2;
        text-align: center;
        font-size: 2.4px;
        font-weight: 500;
        margin: -$unit*2 $unit*3;
    }
}
